section{
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 102px);
}
section#about, section#highlights{
  flex-direction: column;
}

.liquidlogo{
  width: 15vw;
  min-width: 250px;
}

.parallax {
  position: relative;
  background: #ccc;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  justify-content: center;
  align-items: start;
  box-shadow: inset 0 0 0 1000px rgba(40, 40, 50, 0.8);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@keyframes flickerAnimation {
  0%   { opacity:0; }
  50%  { opacity:1; }
  100%  { opacity:0; }
}
@-webkit-keyframes flickerAnimation{
  0%   { opacity:0; }
  50%  { opacity:1; }
  100%  { opacity:0; }
}
.fade {
   -webkit-animation: flickerAnimation 3000ms infinite;
    animation: flickerAnimation 3000ms infinite;
}

