
section{
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 102px);
}
section#about, section#highlights{
  flex-direction: column;
}

.liquidlogo{
  width: 15vw;
  min-width: 250px;
}

.parallax {
  position: relative;
  background: #ccc;
  height: fit-content;
  display: flex;
  justify-content: center;
  align-items: start;
  box-shadow: inset 0 0 0 1000px rgba(40, 40, 50, 0.8);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@keyframes flickerAnimation {
  0%   { opacity:0; }
  50%  { opacity:1; }
  100%  { opacity:0; }
}
@-o-keyframes flickerAnimation{
  0%   { opacity:0; }
  50%  { opacity:1; }
  100%  { opacity:0; }
}
@-moz-keyframes flickerAnimation{
  0%   { opacity:0; }
  50%  { opacity:1; }
  100%  { opacity:0; }
}
@-webkit-keyframes flickerAnimation{
  0%   { opacity:0; }
  50%  { opacity:1; }
  100%  { opacity:0; }
}
.fade {
   -webkit-animation: flickerAnimation 3000ms infinite;
   -moz-animation: flickerAnimation 3000ms infinite;
   -o-animation: flickerAnimation 3000ms infinite;
    animation: flickerAnimation 3000ms infinite;
}

.dragon{
  position: relative;
  top: -156px;
  left: -8vw;
  margin-bottom: -156px;
}
.parallax h1{
  font-size: max(5vw, 36px);
  cursor: default;
  text-align: center;
  color: #fff;
  margin-bottom: 50px;
}

.about-buttons a{
  margin-bottom: 10px
}
.intro {
  color: #fff;
  background: rgba(0, 0, 0, 0.4);
  padding: 4rem 6rem 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  /* // transform: translateY(-15rem); */
  max-width: 700px;
  margin: 0 auto;
}
.intro p {
  letter-spacing: 1px;
  word-spacing: 2px;
  font-weight: 300;
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.intro h1 {
  font-size: 6rem;
  font-weight: 200;
  line-height: 1.2;
  margin-bottom: 1.5rem;
}
  .intro h1 b {
    font-weight: 400;
  }


.intro a {
  transform: translateY(50%);
  align-self: flex-end;
}


.form {
  position: absolute;
  margin: auto auto;
  bottom: 25%;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap:20px;
  align-items: center;
  justify-content: center;
}
.form a{
  width: 80%;
  max-width: 700px;
  background: url('../../../assets/logo.png'), rgb(34, 50, 97) !important;
  background-size: 400px, cover !important;
  background-repeat: no-repeat, no-repeat !important;
  background-attachment: fixed !important;
  background-position: center !important;
}

@media (max-width: 768px) {
  .intro {
    padding: 4rem 4rem 0;
  }
  .intro h1 {
    font-size: 5rem;
  }
  
}

@media (max-width: 576px) {
  .intro {
    padding: 2rem 1rem 0 1rem;
    align-items: center;
    text-align: center;
  }
  .intro h1 {
    font-size: 3rem;
  }

  .intro a {
    align-self: center;
    margin-top: -2rem;
  }
  .dragon{
    top: -130px;
    left: -40vw;
    margin-bottom: -156px;
  }
}
